
import slide from 'components/slide/index'
import http from 'js/http'
import XDialog from 'vux/src/components/x-dialog/index.vue'


export default {
name: "index",
components: {
slide,
XDialog
},
data() {
return {
dataList: [],
page: 1,
loadShow: true,
show:false,
btnLoading: false,
redeemId:'',
total:''
}
},
methods: {
handRedeem() {
this.btnLoading = true
http.post('/vote/revoke-vote', {
id:this.redeemId
}, (res) => {
this.btnLoading = false
if (res.code === 0){
this.show = false
this.redeemId = ''
}
this.$vux.toast.show(res.msg)
this.cleanUpData()
})
},
cleanUpData(){
this.dataList = []
this.page = 1
this.loadShow = true
this.getList()
},
clickRedeem(id){
this.redeemId = id
this.show = true
},
handleBottom() {
if (this.total!==''&&this.dataList.length >= parseInt(this.total)){
this.$refs.my_scroller.finishInfinite(true);
return
}

http.post('/vote/logs', {
page: this.page,
page_size: 10,
type:1
}, (res) => {
if (this.loadShow) this.loadShow = false
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.dataList = this.dataList.concat(res.content.list)
this.total = res.content.count
this.page++
this.$refs.my_scroller.finishInfinite(false);
})
},
getList() {
http.post('/vote/logs', {
page: this.page,
page_size: 10,
type:1
}, (res) => {
if (this.loadShow) this.loadShow = false
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.dataList = this.dataList.concat(res.content.list)
if (this.dataList.length < parseInt(res.content.count)) {
this.$refs.vueLoad.onBottomLoaded();
} else {
this.$refs.vueLoad.onBottomLoaded();
}
})
},
},
created() {
}
}



